import {createSlice} from "@reduxjs/toolkit";

const initialStockExpertisesState = {
  listLoading: false,
  actionsLoading: false,
  totalCount: 0,
  entities: [],
  stockExpertiseForEdit: undefined,
  lastError: null
};
export const callTypes = {
  list: "list",
  action: "action"
};

export const stockExpertisesSlice = createSlice({
  name: "stockExpertises",
  initialState: initialStockExpertisesState,
  reducers: {
    catchError: (state, action) => {
      state.error = `${action.type}: ${action.payload.error}`;
      if (action.payload.callType === callTypes.list) {
        state.listLoading = false;
      } else {
        state.actionsLoading = false;
      }
    },
    startCall: (state, action) => {
      state.error = null;
      if (action.payload.callType === callTypes.list) {
        state.listLoading = true;
      } else {
        state.actionsLoading = true;
      }
    },
   
    // getStockExpertiseById
    stockExpertiseFetched: (state, action) => {
      state.actionsLoading = false;
      state.stockExpertiseForEdit = action.payload.stockExpertiseForEdit;
      state.error = null;
    },
    // findStockExpertises
    stockExpertisesFetched: (state, action) => {
      const { totalCount, entities } = action.payload;
      state.listLoading = false;
      state.error = null;
      state.entities = entities;
      state.totalCount = totalCount;
    },
    
    // createStockExpertise
    stockExpertiseCreated: (state, action) => {
      state.actionsLoading = false;
      state.error = null;
      state.entities.push(action.payload.stockExpertise);
    },
    // updateStockExpertise
    stockExpertiseUpdated: (state, action) => {
      state.error = null;
      state.actionsLoading = false;
      state.entities = state.entities.map(entity => {
        if (entity.id === action.payload.stockExpertise.id) {
          return action.payload.stockExpertise;
        }
        return entity;
      });
    },
    // deleteStockExpertise
    stockExpertiseDeleted: (state, action) => {
      state.error = null;
      state.actionsLoading = false;
      state.entities = state.entities.filter(el => el.id !== action.payload.id);
    }

  }
});
